import React,{useState} from 'react'
import './view.css'

import FlipMove from "react-flip-move";
export default function View({noteData,setNoteData}) {
    function deleteNote(value){
      
            console.log(value.id+" deleted")
            setNoteData(noteData.filter((e)=>{
                return e!==value
            }))
        
    }
    function updateState(title,key){
        console.log("key :", key);
        console.log("update1")
        let newTitle=noteData.map((value)=>{
            if(value.id!==key){
                return value
            }else{
                
                return {id:key,title,note:value.note}
            }
        })
        setNoteData(()=>{
            return newTitle
        })
        console.log(newTitle);
        console.log("update1")
        localStorage.setItem("notesLocal", JSON.stringify(noteData));

    }
    function updateState2(note,key){
        console.log("update12")
        let newNote=noteData.map((value)=>{
            if(value.id!==key){
                return value
            }else{
                
                return {id:key, title:value.title,note:note}
            }
        })
        setNoteData(()=>{
            return newNote
        })
        console.log("update12"+newNote)
        localStorage.setItem("notesLocal", JSON.stringify(noteData));
    }
    let [sty,setStyle]=useState({ width:"400px",
    margin:"20px"})


    let FlexArea={
        display:"flex",
        alignItems:"center",
        justifyContent:"space-between"
    }
    function change(){
        setStyle(()=>{
            return { width:"500px",
            margin:"20px"}
        })
    }
    
    
    return (
        <div className="container">
            <button onClick={()=>{change()}} className="btn btn-warning">Click</button>
           <div  > 
           <FlipMove className='row' duration={1000}>
           {noteData.map((valueOFData,index)=>{
               return(
                   <div className='col-md-4 mt-3' key={index}>










                    <div className='card'>
                        
                    <div className='card-title'>  
                    <input className="invisible-input m-3" onChange={(e)=>{updateState(e.target.value,valueOFData.id)}} value= {valueOFData.title} />
                   
                    </div>
                    
                    <div className='card-body'><input  className="invisible-input" onChange={(e)=>{updateState2(e.target.value,valueOFData.id)}}  value={valueOFData.note} />   </div>
                    <div className='card-footer'> 
                    <div className='d-flex justify-content-between'> 
                    
                    <button onClick={(e)=>{e.preventDefault();deleteNote(valueOFData)}} className='btn btn-danger'>  Delete </button>
                      </div>

                    </div>
                    </div>
                    </div>
               )
           })}</FlipMove>
        </div> 
        </div>
    )
}
