import React,{useState} from "react";

import { Link } from "react-router-dom";

export default function Header() {
    let [divElement,setDiv]=useState(<span></span>)
   let linkStyle={
       fontWeight:500,
       padding: '0.5em 1em' ,
       cursor:'pointer'
       
   }
    function dropDown() {
        let drop={
            backgroundColor:'#fcfcfc',
            zIndex:100000,
            boxShadow:'rgb(0 0 0 / 24%) 0px 8px 20px',
            borderRadius:"0 0 15px 15px",
            cursor:'pointer',

        }
        setDiv(()=>{
            return(
                <div style={drop}  onMouseOver={()=>{dropDown()}} className='position-absolute'> 
                <Link style={linkStyle} className="nav-link" aria-current="page" to="/note">Note</Link>
                <Link style={linkStyle}  className="nav-link" aria-current="page" to="/view">Show Notes</Link>
                </div>
            )
        })
    }
    function clearDiv(){
        setDiv(()=>{
            return(''
            )
        })
    }

    
  return (
    <div className="">
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="container">
          <Link style={linkStyle} className="navbar-brand" to="/">
            {" "}
            Nano<span style={{color:'#ff6060'}}>Tex</span>
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link style={linkStyle} className="nav-link active" aria-current="page" to="/">
                  {" "}
                  Home
                </Link>
               
              </li>
              <li className="nav-item position-relative">
                <div style={linkStyle} onClick={()=>{clearDiv()}} onMouseOver={()=>{dropDown()}} className="nav-link" >
                  {" "}
                  Services
                </div>
                {divElement}
              </li>

              <li className="nav-item">
                <Link style={linkStyle} className="nav-link" to="/">
                  {" "}
                  intro
                </Link>
              </li>
              <li className="nav-item">
                <Link style={linkStyle} className="nav-link" to="/about">
                  {" "}
                  About Us
                </Link>
              </li>
            </ul>
            <form className="d-flex">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button className="btn btn-outline-success" type="submit">
                Search
              </button>
            </form>
          </div>
        </div>
      </nav>
    </div>
  );
}
