import React from "react";
import FlipMove from "react-flip-move";

 class Footer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  add() {
    let NewData = "hello";
    this.setState({
      data: [ ...this.state.data, NewData ]
    });
  }
  render() {
     let now=this.state.data
      let data=now.map((value,index)=>{
        return(
            <div className='m-3' key={index}> {value}  </div>
        )
    })
      
    return (
      <div className="container mt-5">
        <button onClick={() => this.add()} className="btn btn-warning">
          Click{" "}
        </button>
<FlipMove duration={1000} easing="ease-in-out">{data}</FlipMove>
       
      </div>
    )
  }
}
export default Footer