import React, { useState, useEffect } from "react";
import Header from "./header/header";
import Intro from "./intro/intro";
import Note from "./note/note";
import About from "./about/about";
import View from './view/view';
import Footer from './footer/footer';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
// import FlipMove from "react-flip-move";
export default function Main() {
    let localNotes;
  if(localStorage.getItem("notesLocal")===null){
    localStorage.setItem("notesLocal",JSON.stringify(''))
    localNotes=JSON.parse(localStorage.getItem("notesLocal"));
  }else{
    localNotes=JSON.parse(localStorage.getItem("notesLocal"));
  }  
  let [noteData, setNoteData] = useState(localNotes);
  let [notes, setNotes] = useState("");
  let [notesTitle, setNotesTitle] = useState("");
  useEffect(()=>{
    localStorage.setItem("notesLocal", JSON.stringify(noteData));
  },[noteData])
function saveData(){

    let NOTE={
        id:Date.now(),
        title:notesTitle,
        note:notes
    }
    setNoteData((prev)=>{
        return [...prev,NOTE]
    })
    console.log(noteData)
}
  return (
    <div>
      
      <Router>
        <Header />
        <Switch>
          <Route exact path="/intro">
            <Intro />
          </Route>
          <Route exact path="/note">
            <Note
              notesTitle={notesTitle}
              setNotesTitle={setNotesTitle}
              notes={notes}
              setNotes={setNotes}
              saveData={saveData}
            />
          </Route>
          <Route exact path="/about">
            <About />
          </Route>
          <Route exact path="/view">
            <View setNoteData={setNoteData}  noteData={noteData} />
          </Route>
        </Switch>
        <Route exact path="">
        <Footer/>
        </Route>
      </Router>
      
    </div>
  );
}
