import React from 'react'

export default function Note({notes,setNotes,notesTitle,setNotesTitle,saveData}) {
    let textArea={
        minHeight:'10em',
    }
    return (
        <div className="mt-5">
            <div className='container'>  
            <form onSubmit={(e)=>{e.preventDefault();saveData()}}>

            <input placeholder="Title" className="form-control" value={notesTitle} onChange={(e)=>{setNotesTitle(e.target.value)}}/>
            <textarea placeholder="Notes" style={textArea}  className="form-control mt-3" value={notes} onChange={(e)=>{setNotes(e.target.value)}}>
            
            </textarea>
            <button onClick={(e)=>{e.preventDefault();saveData()}} type="submit" className='btn btn-primary mt-3'> Save  </button>
            </form>
             </div>
        </div>
    )
}
